.contact-preview {
    background-color: #96c5ee7e;
    box-shadow: 0 0 5px #0000002b;
    display: flex;
    gap: 10px;
    // flex-direction: column;
    padding: 10px;
    cursor: pointer;
    .contact {
        align-items: center;
        display: flex;
        gap: 5px;
    }
    &:hover {
        background-color: #96c5ee38;
        transition: .2s;
    }
    &>a {
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: space-between;
        gap: 10px;
        .actions {
            display: flex;
            flex-direction: column;
            justify-self: flex-end;
            a {
                font-family: fa-solid;
                font-size: 20px;
                padding: 5px 8px;
                border-radius: 4px;
                &::before {
                    content: '\f4ff'
                }
                &:hover {
                    background-color: rgba(211, 211, 211, 0.253);
                }
            }
            button {
                background-color: transparent;
                border: none;
                font-family: fa-solid;
                font-size: 20px;
                padding: 5px 8px;
                border-radius: 4px;
                &::before {
                    content: '\f503'
                }
                &:hover {
                    background-color: rgba(211, 211, 211, 0.253);
                }
            }
        }
    }
    img {
        background: #fff;
        height: 60px;
        border-radius: 3rem;
    }
    // .info {
