        .contact-edit {
            h1 {
                text-align: center;
            }
            section {
                background-color: rgba(236, 236, 236, 0.692);
                margin: 0 auto;
                width: 400px;
                padding: 50px;
                border-radius: 4px;
                box-shadow: 0px 0px 4px 1px #46464666;
                form {
                    margin-top: 40px;
                    display: flex;
                    flex-direction: column;
                    align-items: center;
                    gap: 5px;
                    // width: 100%;
                    > :is(input) {
                        margin-bottom: 20px;
                        padding: 15px 25px;
                        width: 100%;
                        // border: none;
                        border: 1px solid rgb(60, 60, 60);
                        border-radius: 3px;
                    }
                }
            }
        }