.contact-page {
    .filter-add-container {
        display: flex;
        justify-content: space-between;
        margin-bottom: 20px;
        align-items: center;
        a {
            font-family: fa-solid;
            font-size: 20px;
            padding: 5px 8px;
            color: white;
            background-color: rgb(78, 78, 77);
            border-radius: 4px;
            height: min-content;
            &::before {
                content: '\f234'
            }
            &:hover {
                background-color: rgba(78, 78, 78, 0.637);
            }
        }
    }
}