.contact-details {
    text-align: center;
    section {
        background-color: rgba(236, 236, 236, 0.692);
        margin: 0 auto;
        width: 400px;
        padding: 50px;
        border-radius: 4px;
        box-shadow: 0px 0px 4px 1px #46464666;
        span {
            font-family: fa-solid;
        }
        .phone-icon {
            &::before {
                content: '\f879';
            }
        }
        .email-icon {
            &::before {
                content: '\f1fa';
            }
        }
    }
}