.signup-page {
    text-align: center;
    // margin: 0 200px;
    section {
        background-color: rgba(236, 236, 236, 0.692);
        margin: 0 auto;
        width: 350px;
        padding: 50px;
        border-radius: 4px;
        box-shadow: 0px 0px 4px 1px #46464666;
        img {
            height: 100px;
        }
        form {
            display: flex;
            gap: 10px;
            flex-direction: column;
            align-items: center;
            input {
                padding: 10px 15px;
                // border: none;
                border: 1px solid rgb(60, 60, 60);
                border-radius: 3px;
            }
        }
    }
}