.app-header {
    background-color: rgb(104, 141, 184);
    display: flex;
    height: 80px;
    justify-content: space-between;
    align-items: center;
    padding-left: 25px;
    padding-right: 25px;
    box-shadow: 0 1px 6px black;
    .logo {
        font-family: VanillaDreamers;
        span {
            // font-size: 15px;
            font-family: fa-brands;
            &::before {
                content: '\f379'
            }
        }
    }
    .links {
        font-size: 20px;
        display: flex;
        height: 80px;
        gap: 20px;
        a {
            padding-left: 10px;
            padding-right: 10px;
            display: flex;
            justify-content: center;
            align-items: center;
            // margin-bottom: -5px;
            &:hover {
                color: whitesmoke;
                background-color: rgba(99, 99, 99, 0.26);
                // border-bottom: 5px solid black;
                // margin-bottom: 0px;
                box-shadow: inset 0px -5px 0 black;
                transition: 0.3s;
            }
        }
    }
}