.transfer-fund {
    form {
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 5px;
        input {
            padding: 10px 15px;
            // border: none;
            font-size: 18px;
            border: 1px solid rgb(60, 60, 60);
            border-radius: 3px;
        }
        button {
            border: none;
            font-size: 20px;
            padding: 5px 8px;
            color: white;
            background-color: rgb(78, 78, 77);
            border-radius: 4px;
            span {
                font-family: fa-brands;
                &::before {
                    content: '\f379';
                }
            }
            &:hover {
                background-color: rgba(78, 78, 78, 0.637);
            }
        }
        small {
            color: red;
        }
    }
}